<template>
<td class="name">
  <div class="people-name">
    <people-avatar class="element avatar" :person="entry"></people-avatar>
    <people-name class="element" :person="entry"></people-name>
  </div>
</td>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import PeopleAvatar from '../widgets/PeopleAvatar'
import PeopleName from '../widgets/PeopleName'

export default {
  name: 'people-name-cell',
  components: {
    PeopleName,
    PeopleAvatar
  },
  props: [
    'entry'
  ],
  computed: {
    ...mapGetters([
    ])
  },
  methods: {
    ...mapActions([
    ])
  }
}
</script>

<style scoped>
.people-name {
  display: flex;
  align-items: center;
  flex-direction: row;
}

.avatar {
  margin-right: 1em;
}

.element {
  flex:1
}
</style>



